
body{
  overflow-x: hidden;
  background: center  url("./img/calendar_backdrop.jpg")
}

.over img {
  margin-left: auto;
  margin-right: auto;
  display: block;
}
.MuiGrid-root .MuiGrid-item .item .MuiAvatar-root {
    position: absolute;
    z-index: 1000;
    /* left: 50%;
    top: 50%; */
    transform: translate(-50%,-50%);
    width: 200px;
    height: 200px;
    background-color: rgba(255, 0, 0, 0.7);
    font-family: "Dancing Script", cursive;
    font-weight: 700;
    font-size: 4rem;
    
}

.item{
  left: 50%; 
   top: 200px; 
   position: relative;
}

.iframe-container {
  overflow: hidden;
  width: 500px;
  height:500px;

}

.iframe-container iframe {
   border: 0;
   height: 100%;
   left: 0;
   position: relative;
   top: 0;
   width: 100%;
}

.bibleContent{
  position: relative;
  top: 50%;
  left: 80%; 
  transform: translate(-50%,-50%);
  font-family: 'Lobster', cursive;
  font-size: 1.5rem;
  white-space: nowrap;
}
.bibleVerse{
  position: absolute;
  top: 40%;
  left: 50%;
   transform: translate(-50%,-50%);
  font-family: 'Lobster', cursive;
  font-size: 1.5rem;
  white-space: nowrap;
}

.text{
  /* position: relative;
  top: 50%;
  border-right: 2px solid; */
  font-size: 20px;
  font-family: 'Indie Flower', cursive;
  font-weight: bold;
  /* text-align: center;
  white-space: nowrap;
  overflow: hidden; */
}

.MuiGridListTile-tile .open {
    animation: wobble 5s 1;

}
.MuiGrid-root .MuiGrid-item[wobble='1']{
  animation: wobble 3s 1;
}

@keyframes wobble {
  25% {
    transform: rotate(15deg);
  }
  50% {
    transform: rotate(-30deg);
  }
  75% {
    transform: rotate(5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@media (min-width: 300px) and (max-width: 767px) {
  
  body{
    overflow-x: hidden;
    width: 100%;
  }
  
  .MuiGrid-root .MuiGrid-container {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    justify-items: center;
    align-items: center;
    padding-left: 0;
  }

  .logoImage{
    width: 60vw;
    height:auto;
  }
  .itemImage{
    width: 250px;
    height: 250px;
  }
  .MuiGrid-root .MuiGrid-item .item .MuiAvatar-root {
    z-index: 1000;
    transform: translate(-50%,-50%);
    width: 100px;
    height: 100px;
    background-color: rgba(255, 0, 0, 0.7);
    font-family: "Dancing Script", cursive;
    font-weight: 700;
    font-size: 4rem;
  }

  .item{
    left: 50%; 
     top: 120px; 
     position: relative;
  }
  
  .iframe-container {
    overflow: hidden;
    width: max-content;
    height:300px;
  
  }
  .iframe-container iframe {
    border: 0;
    height: 100%;
    left: 0;
    position: relative;
    top: 0;
    width: 100%;
 }

